import React, { useEffect, useState } from 'react';
import "../css/components.css";

function Machinery(props) {
    
    const [counter, setCounter] = useState(0);
    const timer = () => setCounter(counter - 1);
    var factor = 1.1;

    function format(input){
        var suffix = [
            '', 'K', 'M', 'B', 'T', 'Qa', 'Qi', 'Sx', 'Sp', 'Oc', 'No', 'Dc', 'Ud',
            'Dd', 'Td', 'Qad', 'Qid', 'Sxd', 'Spd', 'Od', 'Nd', 'V', 'Uv', 'Dv',
            'Tv', 'Qav', 'Qiv', 'Sxv', 'Spv', 'Ov', 'Nv', 'Tg', 'Utg', 'Dtg', 'Ttg',
            'Qatg', 'Qitg', 'Sxtg', 'Sptg', 'Otg', 'Ntg', 'Qaa', 'Uqa', 'Dqa', 'Tqa',
            'Qaqa', 'Qiqa', 'Sxqa', 'Spqa', 'Oqa', 'Nqa', 'Qia', 'Uqi', 'Dqi',
            'Tqi', 'Qaqi', 'Qiqi', 'Sxqi', 'Spqi', 'Oqi', 'Nqi', 'Sxa', 'Usx',
            'Dsx', 'Tsx', 'Qasx', 'Qisx', 'Sxsx', 'Spsx', 'Osx', 'Nsx', 'Spa',
            'Usp', 'Dsp', 'Tsp', 'Qasp', 'Qisp', 'Sxsp', 'Spsp', 'Osp', 'Nsp',
            'Og', 'Uog', 'Dog', 'Tog', 'Qaog', 'Qiog', 'Sxog', 'Spog', 'Oog',
            'Nog', 'Na', 'Un', 'Dn', 'Tn', 'Qan', 'Qin', 'Sxn', 'Spn', 'On',
            'Nn', 'Ct', 'Uc'
        ];
        let logResult = Math.floor(Math.log10(input) / (3 * Math.log10(10)));
        if (input <= 999 && input > 0) {logResult = 0;}
        if (input <= 0) {return 0;}
        return ((Math.floor(input / Math.pow(1000, logResult), 3))).toString() + suffix[logResult];
    }

    function buyLemontree() {
        if (props.money >= Math.floor(props.lemontree === 0 ? 20 : 20*Math.pow(factor, props.lemontree))) {
            props.setMoney(props.money-Math.floor(props.lemontree === 0 ? 20 : 20*Math.pow(factor, props.lemontree)));
            props.setLemontree(props.lemontree+1);
        }
    }

    function buySqueezer() {
        if (props.money >= Math.floor(props.squeezer === 0 ? 100 : 100*Math.pow(factor, props.squeezer))) {
            props.setMoney(props.money-Math.floor(props.squeezer === 0 ? 100 : 100*Math.pow(factor, props.squeezer)));
            props.setSqueezer(props.squeezer+1);
        }
    }

    function buyMixer() {
        if (props.money >= Math.floor(props.mixer === 0 ? 250 : 250*Math.pow(factor, props.mixer))) {
            props.setMoney(props.money-Math.floor(props.mixer === 0 ? 250 : 250*Math.pow(factor, props.mixer)));
            props.setMixer(props.mixer+1);
        }
    }

    function buyBooth() {
        if (props.money >= Math.floor(props.booth === 0 ? 500 : 500*Math.pow(factor, props.booth))) {
            props.setMoney(props.money-Math.floor(props.booth === 0 ? 500 : 500*Math.pow(factor, props.booth)));
            props.setBooth(props.booth+1);
        }
    }

    function buyAcid() {
        if (props.money >= Math.floor(props.acid === 0 ? 1200 : 1200*Math.pow(factor, props.acid))) {
            props.setMoney(props.money-Math.floor(props.acid === 0 ? 1200 : 1200*Math.pow(factor, props.acid)));
            props.setAcid(props.acid+1);
        }
    }

    function buyResearch() {
        if (props.money >= Math.floor(props.research === 0 ? 2600 : 2600*Math.pow(factor, props.research))) {
            props.setMoney(props.money-Math.floor(props.research === 0 ? 2600 : 2600*Math.pow(factor, props.research)));
            props.setResearch(props.research+1);
        }
    }

    function tickeverySecond() {
        var tlemon = props.lemons;
        var tjuice = props.juice;
        var tlemonade = props.lemonade;
        var tmoney = props.money;
        var telectricity = props.electricity;
        var tscience = props.science;
        var i = 0;
        if (props.research >= 1) 
            for (i = 0; i < props.research; i++) 
                if (telectricity >= Math.pow(2, props.scienceex)*5) {
                    telectricity-=Math.pow(2, props.scienceex)*5;
                    tscience += Math.pow(2, props.scienceex);
        }
        if (props.acid >= 1) 
            for (i = 0; i < props.acid; i++) 
                if (tjuice >= Math.pow(2, props.electricityex)*12) {
                    tjuice-=Math.pow(2, props.electricityex)*12;
                    telectricity += Math.pow(2, props.electricityex);
                } else
                    break;
        if (props.booth >= 1) 
            for (i = 0; i < props.booth; i++) 
                if (tlemonade >= Math.pow(2, props.moneyex)) {
                    tlemonade -= Math.pow(2, props.moneyex);
                    tmoney += Math.pow(2, props.moneyex)*2;
                } else
                    break;
        if (props.mixer >= 1) 
            for (i = 0; i < props.mixer; i++) 
                if (tjuice >= Math.pow(2, props.lemonadeex)*3) {
                    tjuice -= Math.pow(2, props.lemonadeex)*3;
                    tlemonade += Math.pow(2, props.lemonadeex);
                } else
                    break;
        if (props.squeezer >= 1) 
            for (i = 0; i < props.squeezer; i++)
                if (tlemon >= Math.pow(2, props.juiceex)*2) {
                    tlemon-=Math.pow(2, props.juiceex)*2;
                    tjuice+=Math.pow(2, props.juiceex);
                } else
                    break;
        if (props.lemontree >= 1) 
            tlemon += Math.pow(2, props.lemonsex)*props.lemontree;
        
        props.setLemons(tlemon);
        props.setJuice(tjuice);
        props.setLemonade(tlemonade);
        props.setMoney(tmoney);
        props.setElectricity(telectricity);
        props.setScience(tscience);
    }

    useEffect(() => {
        if (counter <= 0) {
            tickeverySecond();
        }
        const id = setInterval(timer, 1000);
        return () => clearInterval(id);
      }, [counter]);

return (
    <div id="middlebar" className="machinery">
        <h2>Machinery:</h2>
        <div id="machinery" className="table">
            <table>
                <thead>
                    <tr>
                        <th className="legend"></th>
                        <th className="clickable clickable lemontree" onClick={buyLemontree}>Lemontree</th>
                        <th className="clickable squeezer" onClick={buySqueezer}>Squeezer</th>
                        <th className="clickable mixer" onClick={buyMixer}>Mixer</th>
                        <th className="clickable booth" onClick={buyBooth}>Sales booth</th>
                        <th className="clickable acidgen" onClick={buyAcid}>Acid Generator</th>
                        <th className="clickable research" onClick={buyResearch}>Research Center</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td className="legend">Amout:</td>
                        <td className="clickable lemontree" onClick={buyLemontree}>{props.lemontree}</td>
                        <td className="clickable squeezer" onClick={buySqueezer}>{props.squeezer}</td>
                        <td className="clickable mixer" onClick={buyMixer}>{props.mixer}</td>
                        <td className="clickable booth" onClick={buyBooth}>{props.booth}</td>
                        <td className="clickable acidgen" onClick={buyAcid}>{props.acid}</td>
                        <td className="clickable research" onClick={buyResearch}>{props.research}</td>
                    </tr>
                    <tr>
                        <td className="legend">Costs:</td>
                        <td className="clickable lemontree" onClick={buyLemontree}>{format(Math.floor(props.lemontree === 0 ? 20 : 20*Math.pow(factor, props.lemontree)))} Dollar</td>
                        <td className="clickable squeezer" onClick={buySqueezer}>{format(Math.floor(props.squeezer === 0 ? 100 : 100*Math.pow(factor, props.squeezer)))} Dollar</td>
                        <td className="clickable mixer" onClick={buyMixer}>{format(Math.floor(props.mixer === 0 ? 250 : 250*Math.pow(factor, props.mixer)))}  Dollar</td>
                        <td className="clickable booth" onClick={buyBooth}>{format(Math.floor(props.booth === 0 ? 500 : 500*Math.pow(factor, props.booth)))}  Dollar</td>
                        <td className="clickable acidgen" onClick={buyAcid}>{format(Math.floor(props.acid === 0 ? 1200 : 1200*Math.pow(factor, props.acid)))}  Dollar</td>
                        <td className="clickable research" onClick={buyResearch}>{format(Math.floor(props.research === 0 ? 2600 : 2600*Math.pow(factor, props.research)))}  Dollar</td>
                    </tr>
                    <tr>
                        <td className="legend">Services per Second:</td>
                        <td className="clickable lemontree" onClick={buyLemontree}>{format(Math.pow(2, props.lemonsex)) + " Free Lemon"}</td>
                        <td className="clickable squeezer" onClick={buySqueezer}>{format(Math.pow(2, props.juiceex)*2) + " Lemons for " + format(Math.pow(2, props.juiceex)) + " Juice"}</td>
                        <td className="clickable mixer" onClick={buyMixer}>{format(Math.pow(2, props.lemonadeex)*3) + " Juice for " + format(Math.pow(2, props.lemonadeex)) + " Lemonade"}</td>
                        <td className="clickable booth" onClick={buyBooth}>{format(Math.pow(2, props.moneyex)) + " Lemonade for " + format(Math.pow(2, props.moneyex)*2) + " Dollar"}</td>
                        <td className="clickable acidgen" onClick={buyAcid}>{format(Math.pow(2, props.electricityex)*12) + " Juice for " + format(Math.pow(2, props.moneyex)) + " Electricity"}</td>
                        <td className="clickable research" onClick={buyResearch}>{format(Math.pow(2, props.science)*5) + " Electricity for " + format(Math.pow(2, props.moneyex)) + " Science"}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
  );
}

export default Machinery;