import React from 'react';
import "../css/components.css";


function Extensions(props) {

    function format(input){
        var suffix = [
            '', 'K', 'M', 'B', 'T', 'Qa', 'Qi', 'Sx', 'Sp', 'Oc', 'No', 'Dc', 'Ud',
            'Dd', 'Td', 'Qad', 'Qid', 'Sxd', 'Spd', 'Od', 'Nd', 'V', 'Uv', 'Dv',
            'Tv', 'Qav', 'Qiv', 'Sxv', 'Spv', 'Ov', 'Nv', 'Tg', 'Utg', 'Dtg', 'Ttg',
            'Qatg', 'Qitg', 'Sxtg', 'Sptg', 'Otg', 'Ntg', 'Qaa', 'Uqa', 'Dqa', 'Tqa',
            'Qaqa', 'Qiqa', 'Sxqa', 'Spqa', 'Oqa', 'Nqa', 'Qia', 'Uqi', 'Dqi',
            'Tqi', 'Qaqi', 'Qiqi', 'Sxqi', 'Spqi', 'Oqi', 'Nqi', 'Sxa', 'Usx',
            'Dsx', 'Tsx', 'Qasx', 'Qisx', 'Sxsx', 'Spsx', 'Osx', 'Nsx', 'Spa',
            'Usp', 'Dsp', 'Tsp', 'Qasp', 'Qisp', 'Sxsp', 'Spsp', 'Osp', 'Nsp',
            'Og', 'Uog', 'Dog', 'Tog', 'Qaog', 'Qiog', 'Sxog', 'Spog', 'Oog',
            'Nog', 'Na', 'Un', 'Dn', 'Tn', 'Qan', 'Qin', 'Sxn', 'Spn', 'On',
            'Nn', 'Ct', 'Uc'
        ];
        let logResult = Math.floor(Math.log10(input) / (3 * Math.log10(10)));
        if (input <= 999 && input > 0) {logResult = 0;}
        if (input <= 0) {return 0;}
        return ((Math.floor(input / Math.pow(1000, logResult), 3))).toString() + suffix[logResult];
    }

    var factor = 2;

    function doubleLemon() {
        if (props.science >= (props.lemonsex === 0 ? 100 : 100*(props.lemonsex*factor))) {
            props.setScience(props.science-(props.lemonsex === 0 ? 100 : 100*(props.lemonsex*factor)));
            props.setLemonsex(props.lemonsex+1);
        }
    }

    function doubleJuice() {
        if (props.science >= (props.juiceex === 0 ? 150 : 150*(props.juiceex*factor))) {
            props.setScience(props.science-(props.juiceex === 0 ? 150 : 150*(props.juiceex*factor)));
            props.setJuiceex(props.juiceex+1);
        }
    }

    function doubleLemonade() {
        if (props.science >= (props.lemonadeex === 0 ? 200 : 200*(props.lemonadeex*factor))) {
            props.setScience(props.science-(props.lemonadeex === 0 ? 200 : 200*(props.lemonadeex*factor)));
            props.setLemonadeex(props.lemonadeex+1);
        }
    }

    function doubleDollar() {
        if (props.science >= (props.moneyex === 0 ? 250 : 250*(props.moneyex*factor))) {
            props.setScience(props.science-(props.moneyex === 0 ? 250 : 250*(props.moneyex*factor)));
            props.setMoneyex(props.moneyex+1);
        }
    }

    function doubleElectircity() {
        if (props.science >= (props.electricityex === 0 ? 500 : 500*(props.electricityex*factor))) {
            props.setScience(props.science-(props.electricityex === 0 ? 500 : 500*(props.electricityex*factor)));
            props.setElectricityex(props.electricityex+1);
        }
    }

    function doubleScience() {
        if (props.science >= (props.scienceex === 0 ? 750 : 750*(props.scienceex*factor))) {
            props.setScience(props.science-(props.scienceex === 0 ? 750 : 750*(props.scienceex*factor)));
            props.setScienceex(props.scienceex+1);
        }
    }

return (
    <div id="middlebar">
        <h2>Extensions:</h2>
        <div id="ressource" className="table">
            
            <table>
                <thead>
                    <tr>
                        <th className="legend"></th>
                        <th className="clickable lemonsex" onClick={doubleLemon}>Double Lemons</th>
                        <th className="clickable juiceex" onClick={doubleJuice}>Double Juice</th>
                        <th className="clickable lemonadeex" onClick={doubleLemonade}>Double Lemonade</th>
                        <th className="clickable dollarex" onClick={doubleDollar}>Double Dollar</th>
                        <th className="clickable electricityex" onClick={doubleElectircity}>Double Electircity</th>
                        <th className="clickable scienceex" onClick={doubleScience}>Double Science</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td className="legend">Costs:</td>
                        <td className="clickable lemonsex" onClick={doubleLemon}>{format(props.lemonsex === 0 ? 100 : 100*(props.lemonsex*factor))} Science</td>
                        <td className="clickable juiceex" onClick={doubleJuice}>{format(props.juiceex === 0 ? 150 : 150*(props.juiceex*factor))} Science</td>
                        <td className="clickable lemonadeex" onClick={doubleLemonade}>{format(props.lemonadeex === 0 ? 200 : 200*(props.lemonadeex*factor))} Science</td>
                        <td className="clickable dollarex" onClick={doubleDollar}>{format(props.moneyex === 0 ? 250 : 250*(props.moneyex*factor))} Science</td>
                        <td className="clickable electricityex" onClick={doubleElectircity}>{format(props.electricityex === 0 ? 500 : 500*(props.electricityex*factor))} Science</td>
                        <td className="clickable scienceex" onClick={doubleScience}>{format(props.scienceex === 0 ? 750 : 750*(props.scienceex*factor))} Science</td>
                    </tr>
                    <tr>
                        <td className="legend">Services per Action:</td>
                        <td className="clickable lemonsex" onClick={doubleLemon}>Get Twice much Lemons</td>
                        <td className="clickable juiceex" onClick={doubleJuice}>Get Twice much Juice</td>
                        <td className="clickable lemonadeex" onClick={doubleLemonade}>Get Twice much Lemonade</td>
                        <td className="clickable dollarex" onClick={doubleDollar}>Get Twice much Dollar</td>
                        <td className="clickable electricityex" onClick={doubleElectircity}>Get Twice much Electricity</td>
                        <td className="clickable scienceex" onClick={doubleScience}>Get Twice much Science</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
  );
}

export default Extensions;