import React, { useState } from 'react';
import './App.css';
import Resources from "./Components/Ressources";
import Machinery from "./Components/Machinery";
import Extensions from "./Components/Extensions";

function App() {

  const[lemons, setLemons] = useState(0);
  const[juice, setJuice] = useState(0);
  const[lemonade, setLemonade] = useState(0);
  const[money, setMoney] = useState(0);
  const[electricity, setElectricity] = useState(0);
  const[science, setScience] = useState(0);

  const[lemontree, setLemontree] = useState(0);
  const[squeezer, setSqueezer] = useState(0);
  const[mixer, setMixer] = useState(0);
  const[booth, setBooth] = useState(0);
  const[acid, setAcid] = useState(0);
  const[research, setResearch] = useState(0);
  
  const[lemonsex, setLemonsex] = useState(0);
  const[juiceex, setJuiceex] = useState(0);
  const[lemonadeex, setLemonadeex] = useState(0);
  const[moneyex, setMoneyex] = useState(0);
  const[electricityex, setElectricityex] = useState(0);
  const[scienceex, setScienceex] = useState(0);

  return (
    <>
    <Resources lemons={lemons} lemonsex={lemonsex} setLemons={setLemons} juice={juice} juiceex={juiceex} setJuice={setJuice} lemonade={lemonade} lemonadeex={lemonadeex} setLemonade={setLemonade} money={money} moneyex={moneyex} setMoney={setMoney} electricity={electricity} electricityex={electricityex} setElectricity={setElectricity} science={science} scienceex={scienceex} setScience={setScience}/>
    <Machinery research={research} setResearch={setResearch} acid={acid} setAcid={setAcid} booth={booth} setBooth={setBooth} mixer={mixer} setMixer={setMixer} squeezer={squeezer} setSqueezer={setSqueezer} lemontree={lemontree} setLemontree={setLemontree} lemons={lemons} lemonsex={lemonsex} setLemons={setLemons} juice={juice} juiceex={juiceex} setJuice={setJuice} lemonade={lemonade} lemonadeex={lemonadeex} setLemonade={setLemonade} money={money} moneyex={moneyex} setMoney={setMoney} electricity={electricity} electricityex={electricityex} setElectricity={setElectricity} science={science} scienceex={scienceex} setScience={setScience}/> 
    <Extensions lemonsex={lemonsex} setLemonsex={setLemonsex} juiceex={juiceex} setJuiceex={setJuiceex} lemonadeex={lemonadeex} setLemonadeex={setLemonadeex} moneyex={moneyex} setMoneyex={setMoneyex} electricityex={electricityex} setElectricityex={setElectricityex} scienceex={scienceex} setScienceex={setScienceex} science={science} setScience={setScience}/> 
    </>
  );
}

export default App;
